<template>
  <el-menu
    class="categories"
    default-active="0"

    active-text-color="red">
    <el-menu-item @click=handleSelect(0)>
      <i class="el-icon-menu"></i>
      <span slot="title">全部</span>
    </el-menu-item>
    <el-menu-item  v-for="category in categoryList" @click=handleSelect(category.id) :key="category.id">
      <i class="el-icon-menu"></i>
      <span slot="title">{{category.name}}</span>
    </el-menu-item>
  </el-menu>
</template>

<script>
  export default {
    name: 'SideMenu',
    data () {
      return {
        cid: '',
        categoryList : []
      }
    },
    mounted: function (){
      this.loadCategories()
    },
    methods: {
      handleSelect (cid) {
        this.cid = cid
        this.$emit('indexSelect')
      },
      loadCategories(){
        this.$axios.get('/listCategories').then(resp=>{
          if (resp && resp.status === 200) {
            this.categoryList = resp.data
          }
        })
      }


    }
  }
</script>

<style scoped>
  .categories {
    position: fixed;
    margin-left: 50%;
    left: -600px;
    top: 100px;
    width: 150px;
  }
</style>




